import styled from 'styled-components';

const StyledRecipeCard = styled.div`
	box-shadow: 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
	border-radius: 8px;
	padding: 20px;
	max-width: 800px;
	margin: 0 auto 50px auto;
	display: block;
	background-color: #fff;
`;

export default StyledRecipeCard;



